<style lang="less" scoped>
  // 顶部样式居中
  .header_top_center {
    text-align: center;
    font-size: 36px;
    color: #292929;
    font-weight: 600;
    vertical-align: middle;
    &>img {
      width: 22px;
      height: 12px;
      vertical-align: middle;
      margin-left: 18px;
    }
  }
  .back_btn {
    height: 50px;
    width: auto !important;
  }
  /* HEADER 头部内容 START */
  header {
    display: -webkit-flex;
    display: flex;
    height: 100px;
    position: relative;
    border-bottom: 2px solid #dcdcdc;
    // margin-bottom: 30px;
    &>section{
      width: 100%;
      background-color: #fff;
      position: absolute;
      top: 100px;
      left: 0;
      z-index: 99;
      &>div{
        text-align: center;
      }
    }
  }
  header>div {
    flex: 1;
  }
  header>div:nth-child(1) {
    position: relative;
    &>a{
      position: absolute;
      top: 50%;
      left: 5%;
      transform: translateY(-50%);
    }
  }
  header>div:nth-child(2) {
    padding: 30px 0 0 0;
  }
  header>div:nth-child(2)>a {
    font-size: 1.125rem;
    position: relative;
    display: inline-block;
    &>img{
      position: absolute;
      top: 50%;
      transform: translateX(-50%);
    }
  }
  header>div:nth-child(3) {
    text-align: right;
    padding: 40px 20px 0 0;
  }
  header>div:nth-child(3)>a {
    font-size: 30px;
    color: #0dc04b;
  }
  /* HEADER 头部内容 END*/
  #searchBar{
    width: 100%;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 1;
  }
  /* NAV 导航内容 START */
  nav.work_range {
    width: 100%;
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    height: 70px;
    line-height: 70px;
    background: #fff;
    padding: 30px 0;
    
  }
  nav.work_range>div {
    -webkit-box-flex: 1;
    flex: 1;
    text-align: center;
    position: relative;
    color: #464646;
    font-size: 30px;
  }
  nav>div:nth-child(1)>a {
    color: #3eb140;
  }
  /* NAV 导航内容 END */
  div.active {
    color: #3eb140 !important;
  }
  div.active::after {
    content: "";
    display: block;
    position: absolute;
    width: 24px;
    height: 4px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: #3eb140;
    border-radius: 10px;
  }
  .relatedMe{
    position: relative;
    .related-me-info{
      position: absolute;
      top: 5px;
      right: 20px;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-color: red;
      transform: scale(0.6);
    }
  }
  /* 模态框 */
  .modal_list {
    width: 100%;
    background-color: rgba(255, 255, 255, 1);
  }
  .course_item {
    margin: 20px 0;
    &>span {
      display: inline-block;
      font-size: 25px;
    }
    &>span:nth-child(1) {
      width: 100%;
      height: 60px;
      line-height: 60px;
      text-align: center;
    }
  }
  .submitWorkImg{
    background-image: url('../../assets/img/shouye_remen_tijiao.png');
    background-repeat: no-repeat;
    background-position: center center;
    display: inline-block;
    background-size: 100% 100%;
    width: 150px;
    height: 30px;
    margin-right: 30px;
  }
</style>

<template>
  <div>
    <div id="searchBar" v-header-fixed @touchmove.prevent>
      <header>
        <div> <!-- 占位 --> </div>
        <div class="header_top_center" @click="enterAll">{{workAreaTitle}}<img :style="{transform: showCategiresStatus === 'block' ? 'rotate(180deg)' : 'rotate(0deg)'}" src="../../assets/img/xiala_icon_tizy.png" alt="下拉全部内容"></div>
        <div><a @click="submit_work" class="submitWorkImg"></a></div>
        <section :style="{'display': showCategiresStatus}">
          <div class="modal_list">
          <div class="wrapper" ref="wrapper">
            <div id="loadMore" class="course_item course_item-header" @click="enterCourse('all')"  :style="{color: currentIndex === -1 ? '#3eb140' : ''}"><span >全部学院</span></div>
            <div id="loadMore" class="course_item" :style="{color: currentIndex === index ? '#3eb140' : ''}" v-for="(item, index) in HOME_CATEGORY_LIST" @click="enterCourse(item, index)"  v-if="item.gradeId === '1' && item.categoryId !== 'jio9llum'" :key="index">
              <span>{{item.name}}</span>
            </div>
          </div>
        </div>
        </section>
      </header>

      <nav class="work_range sticky" v-if="tab">
        <router-link :to="{name:workAreaInfo.name,params:{id: $route.params.id} }" tag="div" v-for="(workAreaInfo,index) in workAreaList" :key="index" :class="{relatedMe: workAreaInfo.about}">{{workAreaInfo.text}}<span v-if="workAreaInfo.about && $store.state.newsStatus" class="related-me-info"></span>
        </router-link>
      </nav>
    </div>
    <login v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
    
    <router-view></router-view>
    <homeFooter></homeFooter>
  </div>
</template>
<script>
  import homeFooter from "@/components/homeFooter";
  import login from '@/components/login'
  import { createNamespacedHelpers } from "vuex";
  const { mapMutations, mapActions, mapState } = createNamespacedHelpers("homeModule");
  export default {
    components:{
      login,
      homeFooter
    },
    name: "workArea",
    data() {
      return {
        dropCount: 0,
        searchBarFixed: null,
        tab: true,
        showLoginModal: 0,
        workAreaList: [
          {
            name: 'highestHeat',
            id: this.$route.params.id,
            text: '热门',
            about: 0
          },
          {
            name: 'latestTime',
            id: this.$route.params.id,
            text: '推荐',
            about: 0
          },
          {
            name: 'workRelatedMe',
            id: this.$route.params.id,
            text: '与我相关',
            about: 1
          },
          {
            name: 'myWorkInRange',
            id: this.$route.params.id,
            text: '我的作业',
            about: 0
          },
        ],
        workAreaTitle: '全部学院',
        showCategiresStatus: 'none',
        currentIndex: -1
      }
    },
    computed: {
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      },
      ...mapState([
        'HOME_CATEGORY_LIST'
      ])
    },
    methods: {
      ...mapActions([
        'GET_HOME_CATEGORY'
      ]),
      enterCourse(item, index){
        if(!item.name){
          this.workAreaTitle = '全部学院'
          this.currentIndex = -1
        }else{
          this.workAreaTitle = item.name
          this.currentIndex = index
        }

        this.$store.commit('cleanWorkRange', this.$route.fullPath)
        
        let exPath = this.$route.fullPath.substring(this.$route.fullPath.lastIndexOf('/'), this.$route.fullPath.length)
        if(item === 'all'){
          this.$router.push(`/workArea/all${exPath}`)
        }else{
          if(this.$route.fullPath.indexOf('all') > -1 ){
            this.$router.push(`/workArea/${item.categoryId}${exPath}`)
          }else if(this.$route.fullPath.indexOf('j5m48deg') > -1 ){
            this.$router.push(`/workArea/${item.categoryId}${exPath}`)
          }else if(this.$route.fullPath.indexOf('j5m484vz') > -1 ){
            this.$router.push(`/workArea/${item.categoryId}${exPath}`)
          }
        }
        this.showCategiresStatus = 'none'
      },
      back(){
        this.$router.go(-1);
      },
      submit_work() {
        if(!this.LOGIN_USER_OBJECT){
          this.showLoginModal = 1
          return
        }
        this.$router.push({
          name: 'submitingWork'
        });
      },
      showDialogEvent(data){
        this.showLoginModal = data
      },
      enterAll() {
        if(this.showCategiresStatus ===  'block'){
          this.showCategiresStatus = 'none'
        }else{
          this.showCategiresStatus = 'block'
        }
      }
    },
    created () {
      if(this.$route.fullPath.indexOf('j5m484vz') > -1){
        this.workAreaTitle = '设计学院'
      }else if(this.$route.fullPath.indexOf('j5m48deg') > -1 ){
        this.workAreaTitle = '美术学院'
      }else{
        this.workAreaTitle = '全部学院'
      }
      if(this.HOME_CATEGORY_LIST.length === 0){
        this.GET_HOME_CATEGORY()
      }
    }
  };
</script>



